const createGalleryCard = (imgInfo) => {
  
    const galleryItemEl = document.createElement('li');
    galleryItemEl.classList.add("gallery-item");

    const galleryItemLinkEl = document.createElement('a');
    galleryItemLinkEl.classList.add("gallery-link");
    galleryItemLinkEl.href = imgInfo.original;

    galleryItemEl.append(galleryItemLinkEl);

    const galleryItemImgEl = document.createElement('img');
    galleryItemImgEl.classList.add("gallery-image");
    galleryItemImgEl.src = imgInfo.preview;
    galleryItemImgEl.alt = imgInfo.description;

    galleryItemLinkEl.append(galleryItemImgEl);

    return galleryItemEl;
    
};
const galleryCardsArr = images.map(imgInfo => createGalleryCard(imgInfo));

const galleryListEl = document.querySelector('.gallery');
galleryListEl.append(...galleryCardsArr);

new SimpleLightbox('.gallery a', {
    navText: ['<', '>'],
    captionPosition: 'bottom',
    captionsData: 'alt',
    captionDelay: 250,
    enableKeyboard: true,
    closeText: 'x',
    showCounter: true,
    overlayOpacity: 0.8
});
